import React, { useRef, useState } from 'react'
import { ARSceneAFrame, ARSceneAFrameRef } from '../ARSceneAFrame'
import { ARControls } from '../ARControls'
import { FaceTracker } from '../FaceTracker'
import type { ScreenType } from '../../types/screens'

interface ARScreenProps {
  onNavigate: (screen: ScreenType) => void
  showBackButton?: boolean
}

export const ARScreen: React.FC<ARScreenProps> = ({
  onNavigate,
  showBackButton = true
}) => {
  const sceneRef = useRef<ARSceneAFrameRef>(null)
  const [cameraFacing, setCameraFacing] = useState<'environment' | 'user'>('environment')
  const [faceTrackingEnabled, setFaceTrackingEnabled] = useState<boolean>(true)

  const handleBack = () => {
    onNavigate('tutorial')
  }

  const handleSettings = () => {
    onNavigate('settings')
  }

  const handleToggleFacing = () => {
    const next = cameraFacing === 'environment' ? 'user' : 'environment'
    setCameraFacing(next)
    sceneRef.current?.setCameraFacing(next)
  }

  const handleToggleFaceTracking = () => {
    setFaceTrackingEnabled(prev => !prev)
  }

  return (
    <div className="ar-screen">
      {showBackButton && (
        <div className="ar-screen-header">
          <button 
            className="ar-back-button"
            onClick={handleBack}
          >
            ‚Üê Voltar
          </button>
          
          <button 
            className="ar-settings-button"
            onClick={handleSettings}
          >
            ‚öôÔ∏è
          </button>

        </div>
      )}
      
      <div className="ar-screen-content">
        <ARSceneAFrame 
          ref={sceneRef}
          className="aframe-scene"
          cameraFacing={cameraFacing}
        />
        
        <ARControls 
          sceneRef={sceneRef}
          onToggleFacing={handleToggleFacing}
          onToggleFaceTracking={handleToggleFaceTracking}
          faceTrackingEnabled={faceTrackingEnabled}
        />

        <FaceTracker enabled={faceTrackingEnabled} />
      </div>
    </div>
  )
}
