import React, { useEffect, useRef } from 'react'

interface FaceTrackerProps {
  enabled: boolean
}

declare global {
  interface Window { faceapi?: any }
}

export const FaceTracker: React.FC<FaceTrackerProps> = ({ enabled }) => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null)
  const rafRef = useRef<number | null>(null)
  const modelsLoadedRef = useRef<boolean>(false)
  const statusRef = useRef<HTMLDivElement | null>(null)
  const prevRotRef = useRef<{x:number;y:number;z:number}>({ x: 0, y: 0, z: 0 })
  const prevPosRef = useRef<{x:number;y:number;z:number}>({ x: 0, y: 1, z: -1.2 })
  const maskImgRef = useRef<HTMLImageElement | null>(null)
  const maskLoadedRef = useRef<boolean>(false)
  const isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)

  useEffect(() => {
    function ensureCanvas() {
      if (!canvasRef.current) {
        const c = document.createElement('canvas')
        c.id = 'face-tracker-overlay'
        c.style.position = 'fixed'
        c.style.top = '0'
        c.style.left = '0'
        c.style.width = '100vw'
        c.style.height = '100vh'
        c.style.pointerEvents = 'none'
        c.style.zIndex = '9999'
        document.body.appendChild(c)
        canvasRef.current = c
      }
      if (!statusRef.current) {
        const s = document.createElement('div')
        s.id = 'face-tracker-status'
        s.style.position = 'fixed'
        s.style.left = '8px'
        s.style.bottom = '8px'
        s.style.padding = '4px 8px'
        s.style.background = 'rgba(0,0,0,0.55)'
        s.style.color = '#fff'
        s.style.font = '12px Arial'
        s.style.borderRadius = '6px'
        s.style.zIndex = '10000'
        s.style.pointerEvents = 'none'
        s.textContent = 'FaceTracker: iniciando'
        document.body.appendChild(s)
        statusRef.current = s
      }
    }

    function removeCanvas() {
      if (canvasRef.current && canvasRef.current.parentNode) {
        canvasRef.current.parentNode.removeChild(canvasRef.current)
        canvasRef.current = null
      }
      if (statusRef.current && statusRef.current.parentNode) {
        statusRef.current.parentNode.removeChild(statusRef.current)
        statusRef.current = null
      }
    }

    async function loadFaceApi() {
      if (window.faceapi) return
      statusRef.current && (statusRef.current.textContent = 'FaceTracker: carregando lib')
      await new Promise<void>((resolve, reject) => {
        const script = document.createElement('script')
        script.src = 'https://cdn.jsdelivr.net/npm/face-api.js@0.22.2/dist/face-api.min.js'
        script.onload = () => resolve()
        script.onerror = () => reject(new Error('Falha ao carregar face-api.js'))
        document.head.appendChild(script)
      })
      console.log('face-api.js carregado')
    }

    async function loadModels() {
      if (modelsLoadedRef.current) return
      statusRef.current && (statusRef.current.textContent = 'FaceTracker: carregando modelos')
      const faceapi = window.faceapi
      const MODEL_URL = 'https://raw.githubusercontent.com/justadudewhohacks/face-api.js/master/weights'
      if (isMobile) {
        await Promise.all([
          faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL),
          faceapi.nets.faceLandmark68Net.loadFromUri(MODEL_URL)
        ])
      } else {
        await Promise.all([
          faceapi.nets.tinyFaceDetector.loadFromUri(MODEL_URL),
          faceapi.nets.faceLandmark68Net.loadFromUri(MODEL_URL),
          faceapi.nets.faceExpressionNet.loadFromUri(MODEL_URL)
        ])
      }
      modelsLoadedRef.current = true
      statusRef.current && (statusRef.current.textContent = 'FaceTracker: pronto')
      console.log('Modelos de face-api carregados')
    }

    function loadMask() {
      if (maskLoadedRef.current) return
      const base = (import.meta as any)?.env?.BASE_URL || (document?.baseURI ? new URL(document.baseURI).pathname : '/')
      const normalize = (p: string) => {
        if (!base || base === '/' ) return p.startsWith('/') ? p : `/${p}`
        const b = base.endsWith('/') ? base : base + '/'
        return p.startsWith('/') ? b + p.replace(/^\//, '') : b + p
      }
      const candidatesRaw = [
        'assets/images/mascara.png',   // preferred path
        'images/mascara.png',          // relative images
        'mascara.png'                  // root of public
      ]
      const candidatesBase = candidatesRaw.map(normalize)
      const candidatesFallback = (!base || base === '/') ? ['/mascara.png','/assets/images/mascara.png','/images/mascara.png'] : []
      const candidates = [...candidatesBase, ...candidatesFallback]
      let index = 0

      const img = new Image()
      const tryNext = () => {
        if (index >= candidates.length) {
          console.warn('N√£o foi poss√≠vel carregar mascara.png de nenhum caminho:', candidates)
          return
        }
        const url = candidates[index++] + `?v=${Date.now()}`
        img.src = url
        console.log('BASE_URL resolvido:', base, '| Tentando m√°scara em:', url)
      }

      img.onload = () => {
        maskLoadedRef.current = true
        maskImgRef.current = img
        console.log('M√°scara carregada em:', img.src)
      }
      img.onerror = () => {
        tryNext()
      }

      maskImgRef.current = img
      tryNext()
    }

    function setCanvasSize(canvas: HTMLCanvasElement) {
      const dpr = isMobile ? 1 : Math.max(1, window.devicePixelRatio || 1)
      canvas.width = Math.floor(window.innerWidth * dpr)
      canvas.height = Math.floor(window.innerHeight * dpr)
      const ctx = canvas.getContext('2d')
      if (ctx) ctx.setTransform(dpr, 0, 0, dpr, 0, 0)
    }

    async function start() {
      ensureCanvas()
      await loadFaceApi()
      await loadModels()
      loadMask()

      // Esperar o v√≠deo da c√¢mera existir e estar pronto
      const waitForVideo = (): Promise<HTMLVideoElement> => {
        return new Promise((resolve, reject) => {
          let resolved = false
          const timeout = setTimeout(() => {
            if (!resolved) {
              reject(new Error('Timeout aguardando v√≠deo da c√¢mera'))
            }
          }, 15000)

          const tryResolve = (v: HTMLVideoElement | null) => {
            if (!v) return false
            const hasStream = !!(v as any).srcObject
            if ((v.readyState >= 2 || (!v.paused && v.currentTime > 0)) && hasStream) {
              resolved = true
              clearTimeout(timeout)
              v.removeEventListener('playing', onPlaying)
              observer.disconnect()
              resolve(v)
              return true
            }
            return false
          }

          const onPlaying = (e: Event) => {
            const v = e.target as HTMLVideoElement
            tryResolve(v)
          }

          const observer = new MutationObserver(() => {
            const v = document.getElementById('arjs-video') as HTMLVideoElement | null
            if (tryResolve(v)) return
            if (v) {
              v.addEventListener('playing', onPlaying, { once: true })
            }
          })

          observer.observe(document.body, { childList: true, subtree: true })

          // Checar imediatamente
          const v0 = document.getElementById('arjs-video') as HTMLVideoElement | null
          if (tryResolve(v0)) return
          if (v0) v0.addEventListener('playing', onPlaying, { once: true })
        })
      }

      let video: HTMLVideoElement
      try {
        video = await waitForVideo()
      } catch (e) {
        statusRef.current && (statusRef.current.textContent = 'FaceTracker: sem v√≠deo')
        console.warn('FaceTracker: v√≠deo n√£o dispon√≠vel a tempo', e)
        return
      }

      const canvas = canvasRef.current
      if (!video || !canvas) {
        statusRef.current && (statusRef.current.textContent = 'FaceTracker: sem v√≠deo')
        return
      }

      const faceapi = window.faceapi
      const ctx = canvas.getContext('2d')

      function resizeCanvas() {
        if (canvas) {
          setCanvasSize(canvas)
        }
      }
      resizeCanvas()
      window.addEventListener('resize', resizeCanvas)

      const detect = async () => {
        try {
          if (!enabled) return
          if (!video.videoWidth) {
            statusRef.current && (statusRef.current.textContent = 'FaceTracker: aguardando v√≠deo')
            scheduleNext()
            return
          }

          // iOS/Android: tentar retomar play (gesture unlock simplificado)
          if (isMobile && video.paused) {
            const unlock = () => {
              video.muted = true
              video.play().catch(() => {})
              window.removeEventListener('touchstart', unlock)
              window.removeEventListener('click', unlock)
            }
            window.addEventListener('touchstart', unlock, { passive: true })
            window.addEventListener('click', unlock)
          }

          const isMirrored = (getComputedStyle(video).transform || '').includes('matrix(-1') || (video.style.transform || '').includes('scaleX(-1)')

          const options = new faceapi.TinyFaceDetectorOptions({ inputSize: isMobile ? 160 : 224, scoreThreshold: isMobile ? 0.35 : 0.5 })
          let pipeline: any = window.faceapi
            .detectAllFaces(video, options)
            .withFaceLandmarks()
          if (!isMobile && window.faceapi.nets.faceExpressionNet.isLoaded) {
            pipeline = pipeline.withFaceExpressions()
          }
          const detections = await pipeline

          if (ctx) {
            ctx.clearRect(0, 0, canvas.width, canvas.height)

            // Debug minimal (evitar custo no mobile)
            if (!isMobile) {
              ctx.fillStyle = 'rgba(78,205,196,0.8)'
              ctx.beginPath()
              ctx.arc(16, 16, 6, 0, Math.PI * 2)
              ctx.fill()
              ctx.font = '12px Arial'
              ctx.fillText(`faces: ${detections?.length || 0}`, 30, 20)
            }

            const dpr = isMobile ? 1 : Math.max(1, window.devicePixelRatio || 1)
            const canvasCssWidth = canvas.width / dpr
            const canvasCssHeight = canvas.height / dpr
            const scaleX = canvasCssWidth / video.videoWidth
            const scaleY = canvasCssHeight / video.videoHeight

            const mapX = (x: number, w = 0) => {
              const raw = x * scaleX
              return isMirrored ? (canvasCssWidth - (raw + w)) : raw
            }
            const mapY = (y: number) => y * scaleY

            const drawPoint = (x: number, y: number, label: string, color: string) => {
              if (isMobile) return // pular debug no mobile
              const px = mapX(x)
              const py = mapY(y)
              ctx.fillStyle = color
              ctx.beginPath()
              ctx.arc(px, py, 4, 0, Math.PI * 2)
              ctx.fill()
              ctx.fillStyle = color
              ctx.fillText(label, px + 6, py - 6)
            }

            if (detections && detections.length) {
              statusRef.current && (statusRef.current.textContent = 'FaceTracker: detectando')
              detections.forEach((det: any) => {
                const box = det.detection.box
                const rawW = box.width * scaleX
                const x = mapX(box.x, rawW)
                const y = mapY(box.y)
                const w = rawW
                const h = box.height * scaleY

                if (!isMobile) {
                  ctx.strokeStyle = '#4ECDC4'
                  ctx.lineWidth = 3
                  ctx.strokeRect(x, y, w, h)
                }

                if (!isMobile && det.expressions) {
                  const expr = det.expressions || {}
                  const entries = Object.entries(expr) as Array<[string, number]>
                  if (entries.length) {
                    const [topLabel, topScore] = entries.reduce((a, b) => (a[1] > b[1] ? a : b))
                    const label = `${topLabel} ${(topScore * 100).toFixed(0)}%`
                    ctx.font = '12px Arial'
                    const textW = ctx.measureText(label).width + 12
                    ctx.fillStyle = 'rgba(0,0,0,0.6)'
                    ctx.fillRect(x, Math.max(0, y - 22), textW, 20)
                    ctx.fillStyle = '#FFFFFF'
                    ctx.fillText(label, x + 6, Math.max(12, y - 8))
                  }
                }

                const pts: Array<{x:number;y:number}> = det.landmarks?.positions || []
                if (pts.length === 68) {
                  const noseTip = pts[30]
                  const leftEyeIdx = [36,37,38,39,40,41]
                  const rightEyeIdx = [42,43,44,45,46,47]
                  const avg = (arr: number[]) => arr.reduce((a,b)=>a+b,0)/arr.length
                  const eyeCenter = (ids: number[]) => ({ x: avg(ids.map(i => pts[i].x)), y: avg(ids.map(i => pts[i].y)) })
                  const leftEye = eyeCenter(leftEyeIdx)
                  const rightEye = eyeCenter(rightEyeIdx)

                  if (!isMobile) {
                    drawPoint(noseTip.x, noseTip.y, 'nose', '#00E5A8')
                    drawPoint(leftEye.x, leftEye.y, 'eye-L', '#EF476F')
                    drawPoint(rightEye.x, rightEye.y, 'eye-R', '#EF476F')
                  }

                  const midEyes = { x: (leftEye.x + rightEye.x) / 2, y: (leftEye.y + rightEye.y) / 2 }
                  const midEyesPx = { x: mapX(midEyes.x), y: mapY(midEyes.y) }

                  const dxEyes = rightEye.x - leftEye.x
                  const dyEyes = rightEye.y - leftEye.y
                  let rollDeg = (Math.atan2(dyEyes, dxEyes) * 180) / Math.PI
                  const eyeDist = Math.hypot(dxEyes, dyEyes) || 1
                  let yawDeg = ((noseTip.x - midEyes.x) / eyeDist) * 60
                  let pitchDeg = (-(noseTip.y - midEyes.y) / eyeDist) * 50
                  if (isMirrored) {
                    yawDeg = -yawDeg
                    rollDeg = -rollDeg
                  }
                  const clamp = (v: number, a: number, b: number) => Math.max(a, Math.min(b, v))
                  yawDeg = clamp(yawDeg, -60, 60)
                  pitchDeg = clamp(pitchDeg, -50, 50)
                  rollDeg = clamp(rollDeg, -45, 45)

                  // M√°scara 2D (leve no mobile)
                  if (maskLoadedRef.current && maskImgRef.current) {
                    const rollRad = (rollDeg * Math.PI) / 180
                    const eyeDistPx = eyeDist * scaleX
                    const targetW = eyeDistPx * (isMobile ? 4.2 : 8.0)
                    const ratio = maskImgRef.current.naturalHeight > 0 ? (maskImgRef.current.naturalHeight / maskImgRef.current.naturalWidth) : 1
                    const targetH = targetW * ratio
                    const centerX = mapX(midEyes.x)
                    const centerY = mapY(midEyes.y) + h * 0.10

                    const shearX = (yawDeg / 60) * 0.22
                    const shearY = (pitchDeg / 50) * -0.18
                    const scaleXmask = 1 - Math.min(Math.abs(pitchDeg) / 120, 0.15)
                    const scaleYmask = 1 + (pitchDeg / 50) * 0.14

                    ctx.save()
                    ctx.translate(centerX, centerY)
                    ctx.rotate(rollRad)
                    ctx.scale(scaleXmask, 1)
                    ctx.transform(1, shearY, shearX, scaleYmask, 0, 0)
                    ctx.globalAlpha = 0.95
                    ctx.drawImage(maskImgRef.current, -targetW/2, -targetH*0.55, targetW, targetH)
                    ctx.restore()
                  }

                  const alphaR = 0.25
                  const prevR = prevRotRef.current
                  const smoothedR = {
                    x: prevR.x + (pitchDeg - prevR.x) * alphaR,
                    y: prevR.y + (yawDeg - prevR.y) * alphaR,
                    z: prevR.z + (rollDeg - prevR.z) * alphaR
                  }
                  prevRotRef.current = smoothedR

                  const cameraEl = document.getElementById('camera') as any
                  const headEl = document.getElementById('head-cube') as any
                  if (cameraEl && headEl) {
                    const threeCam = cameraEl.getObject3D && cameraEl.getObject3D('camera')
                    if (threeCam && (window as any).THREE) {
                      const THREE = (window as any).THREE
                      const nx = (midEyesPx.x / canvasCssWidth) * 2 - 1
                      const ny = -((midEyesPx.y / canvasCssHeight) * 2 - 1)
                      const depth = 1.2

                      const origin = new THREE.Vector3()
                      const dir = new THREE.Vector3()

                      const ndc = new THREE.Vector3(nx, ny, 0.5)
                      ndc.unproject(threeCam)
                      origin.copy(threeCam.position)
                      dir.copy(ndc).sub(origin).normalize()

                      const targetPos = origin.clone().add(dir.multiplyScalar(depth))

                      const alphaP = 0.35
                      const prevP = prevPosRef.current
                      const smoothedP = {
                        x: prevP.x + (targetPos.x - prevP.x) * alphaP,
                        y: prevP.y + (targetPos.y - prevP.y) * alphaP,
                        z: prevP.z + (targetPos.z - prevP.z) * alphaP
                      }
                      prevPosRef.current = smoothedP

                      headEl.setAttribute('position', `${smoothedP.x.toFixed(3)} ${smoothedP.y.toFixed(3)} ${smoothedP.z.toFixed(3)}`)
                      headEl.setAttribute('rotation', `${smoothedR.x.toFixed(2)} ${smoothedR.y.toFixed(2)} ${smoothedR.z.toFixed(2)}`)
                    } else if (headEl.setAttribute) {
                      headEl.setAttribute('rotation', `${smoothedR.x.toFixed(2)} ${smoothedR.y.toFixed(2)} ${smoothedR.z.toFixed(2)}`)
                    }
                  }
                }
              })
            } else {
              statusRef.current && (statusRef.current.textContent = 'FaceTracker: sem rosto')
              if (!isMobile) {
                ctx.strokeStyle = 'rgba(255,107,107,0.8)'
                ctx.lineWidth = 2
                ctx.strokeRect(10, 10, 100, 60)
              }
            }
          }
        } catch (e) {
          console.error('FaceTracker detect error:', e)
          statusRef.current && (statusRef.current.textContent = 'FaceTracker: erro de detec√ß√£o')
        }
        scheduleNext()
      }

      const scheduleNext = () => {
        if (isMobile) {
          // Throttle ~12fps para reduzir CPU/bateria
          setTimeout(() => { rafRef.current = requestAnimationFrame(detect) }, 80)
        } else {
          rafRef.current = requestAnimationFrame(detect)
        }
      }

      rafRef.current = requestAnimationFrame(detect)

      return () => {
        window.removeEventListener('resize', resizeCanvas)
      }
    }

    let cleanupResize: (() => void) | undefined

    if (enabled) {
      start().then((c) => {
        cleanupResize = c as any
      }).catch(err => {
        console.error('FaceTracker start error:', err)
        statusRef.current && (statusRef.current.textContent = 'FaceTracker: erro ao iniciar')
      })
    } else {
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current)
        rafRef.current = null
      }
      if (canvasRef.current) {
        const ctx = canvasRef.current.getContext('2d')
        if (ctx) ctx.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height)
      }
      statusRef.current && (statusRef.current.textContent = 'FaceTracker: desligado')
    }

    return () => {
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current)
        rafRef.current = null
      }
      if (cleanupResize) cleanupResize()
      removeCanvas()
    }
  }, [enabled])

  return null
}
