import React, { useState, useEffect, useRef } from 'react'
import { useRA } from '../../contexts/RAContext'
import type { ScreenType } from '../../types/screens'
import Instructions from '../Instructions'

interface TutorialStep {
  id: number
  title: string
  description: string
  image?: string
  action?: string
}

interface TutorialScreenProps {
  onNavigate: (screen: ScreenType) => void
  steps?: TutorialStep[]
  title?: string
  subtitle?: string
  backgroundImage?: string
}

const DEFAULT_STEPS: TutorialStep[] = [
  {
    id: 1,
    title: 'Bem-vindo √† RA',
    description: 'Aprenda a usar a Realidade Aumentada de forma simples e intuitiva.',
    action: 'Toque para continuar'
  },
  {
    id: 2,
    title: 'Adicionar Objetos',
    description: 'Use o seletor de objetos para adicionar cubos, esferas e cilindros √† cena.',
    action: 'Toque para continuar'
  },
  {
    id: 3,
    title: 'Mover Objetos',
    description: 'Toque e arraste os objetos para mov√™-los na cena 3D.',
    action: 'Toque para continuar'
  },
  {
    id: 4,
    title: 'Capturar Imagens',
    description: 'Use o bot√£o "Capturar" para salvar screenshots da sua cena.',
    action: 'Come√ßar RA'
  }
]

export const TutorialScreen: React.FC<TutorialScreenProps> = ({
  onNavigate,
  steps = DEFAULT_STEPS,
  title,
  subtitle,
  backgroundImage
}) => {
  const { raData, loading } = useRA()
  const previousDataRef = useRef<RAData | null>(null)
  const [currentStep, setCurrentStep] = useState(0)
  const step = steps[currentStep]

  // Log apenas quando os dados realmente mudarem
  useEffect(() => {
    if (raData !== previousDataRef.current) {
      previousDataRef.current = raData
    }
  }, [raData])

  const handleNext = () => {
    if (currentStep < steps.length - 1) {
      setCurrentStep(currentStep + 1)
    } else {
      onNavigate('ar')
    }
  }

  const handleSkip = () => {
    onNavigate('ar')
  }

  // Op√ß√£o 1: Usar o componente Instructions (similar ao AF)
  const useInstructionsComponent = true

  if (useInstructionsComponent) {
    return (
      <div className="screen"
        style={{
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
        }}
      >
        <Instructions onPlay={() => onNavigate('ar')} />
      </div>
    )
  }

  // Op√ß√£o 2: Usar o tutorial por passos (original)
  return (
    <div className="tutorial-screen">
      <div className="tutorial-header">
        <button 
          className="tutorial-skip"
          onClick={handleSkip}
        >
          Pular
        </button>
        
        <div className="tutorial-progress">
          {steps.map((_, index) => (
            <div 
              key={index}
              className={`progress-dot ${index === currentStep ? 'active' : ''}`}
            />
          ))}
        </div>
      </div>

      <div className="tutorial-content">
        <div className="tutorial-step">
          <h2 className="step-title">{step.title}</h2>
          <p className="step-description">{step.description}</p>
          
          {step.image && (
            <div className="step-image">
              <img src={step.image} alt={step.title} />
            </div>
          )}
        </div>
      </div>

      <div className="tutorial-footer">
        <button 
          className="tutorial-button"
          onClick={handleNext}
        >
          {step.action}
        </button>
      </div>
    </div>
  )
}
